	module.exports = {"data":{"markdownRemark":{"html":"<p>I've spent the last few days looking for a new note taking and general writing app after <a href=\"http://eddielee.me/notes-woes/\">falling out with</a> Apple’s Notes app.</p>\n<p>The one I've settled on, and has massively exceeded my expectations of what to expect from an iOS writing app is <a href=\"http://www.bear-writer.com\">Bear</a>. In my <a href=\"http://eddielee.me/new-notes-app/\">last blog post</a> I shortlisted several apps I was thinking of moving to, but had missed Bear. Luckily <a href=\"https://twitter.com/weiran/status/813909062135869440\">Weiran Zhang</a> recommended it to me on Twitter.</p>\n<p><img src=\"/images/bear.png\" alt=\"Bear - a beautiful writing app\"></p>\n<p>Bear has the best Markdown support of any app I’ve used, desktop or mobile - and this app works on both. Notes are easily categorised with hashtags. And so far, the sync between my iPhone and Mac has been flawless.</p>\n<p>The app is free with payments of either £1.49/month or £13.99/year for Bear Pro which includes sync.</p>","timeToRead":1,"excerpt":"I've spent the last few days looking for a new note taking and general writing app after  falling out with  Apple’s Notes app. The one I've…","frontmatter":{"title":"Bear - a beautiful writing app","cover":"https://unsplash.it/1152/300/?random?Bear","date":"2016-12-30T18:44","tags":["iOS","macOS","notes","iCloud"],"author":"eddie"},"fields":{"slug":"/bear-a-beautiful-writing-app"}},"prev":{"excerpt":"There has been a lot of talk recently about Russia and potential interference with the US presidential election…","frontmatter":{"title":"Metadata propaganda","cover":"https://unsplash.it/1152/300/?random?Russia","date":"2016-12-30T19:05"},"fields":{"slug":"/metadata-propaganda"}},"next":{"excerpt":"Due to sync issues my  faith in Apple's Notes app has been shaken . I'm aware I can most likely fix this by…","frontmatter":{"title":"Picking a new notes app","cover":"https://unsplash.it/1152/300/?random?NewNotes","date":"2016-12-27T22:30"},"fields":{"slug":"/new-notes-app"}},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"slug":"/bear-a-beautiful-writing-app","total":44,"prev":"/metadata-propaganda","next":"/new-notes-app"}}

